import Layout from '../components/layout';
import Link from 'next/link';

const PostLink = ({title}) => (
    <li>
        <Link href={`/post?title=${title}`}>
        <a>{title}</a></Link> 
    </li>
);

const Post = () => (
    <Layout title="My Blog">
        <ul>
            <PostLink title="React" />
        </ul>
    </Layout>
);

export default Post;